<!--
 * @Description: 分销员分组成员
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="app-container">
    <el-card>
      <div class="header-con">
        组成员业绩
      </div>
    </el-card>
    <el-card class="operate-container"
             style="margin-top: 20px"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="起止时间：">
            <el-date-picker v-model="list.time"
                            value-format="yyyy-MM-dd"
                            type="daterange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>

    <!-- 数据列表 -->
    <el-card class="operate-container"
             style="margin-top:20px"
             shadow="never">
      <div class="main-title">
        <span class="span-yellow">分组名称：<span>{{this.$route.query.groupName}}</span></span>
        <span class="span-yellow">分销成员数量：<span>{{this.total}}</span></span>
        <span class="span-yellow">总累计成交笔数：<span>{{this.$route.query.orderTotal}}</span></span>
      </div>
      <div class="main-title">
        <span class="span-yellow">总累计分销商品交易额：<span>{{this.$route.query.orderAmount}}</span></span>
        <span class="span-yellow">总累计分到佣金：<span>{{this.$route.query.commissionAmount}}</span></span>
        <span class="span-yellow">总累计分到邀请奖励：<span>{{this.$route.query.inviteAmount}}</span></span>
      </div>

    </el-card>

    <div class="table-container">
      <el-table :data="tableList"
                style="width: 100%"
                v-loading="loading"
                @sort-change='tableSortChange'
                ref="multipleTable">
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         min-width="120"
                         show-overflow-tooltip
                         align="center">
          <template slot-scope="scope">{{scope.row.userName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         width="130"
                         align="center">
          <template slot-scope="scope">{{scope.row.cellPhone == '' ? '--' : scope.row.cellPhone}}</template>
        </el-table-column>
        <el-table-column width="180"
                         sortable="custom"
                         prop="orderTotal"
                         align="center"
                         :render-header="renderHeader">
        </el-table-column>
        <el-table-column width="190"
                         sortable="custom"
                         prop="orderAmount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到佣金(不剔除退款)"
                         prop="commissionAmount"
                         width="250"
                         sortable="custom"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到邀请奖励"
                         width="180"
                         sortable="custom"
                         prop="inviteAmount"
                         align="center">
        </el-table-column>
        <el-table-column label="加入时间"
                         width="180"
                         sortable="custom"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="客户数"
                         width="140"
                         sortable="custom"
                         prop="userCount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         min-width="200"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       class="button_mian"
                       @click="showDetails(scope.row)">查看分销员单据</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 固定在下面 -->
    <div class="fixedB">
      <el-button @click="goBack()"
                 type="primary">
        返回上一页
      </el-button>
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[10, 20, 30]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { channelUserList } from '@/api/distributionManagement/performance'
// import { channelUserDetail,setUserBelong } from '@/api/distributionManagement/distributor'
export default {
  data () {
    return {
      list: {
        time: '',
      },
      id: this.$route.query.id,
      name: this.$route.query.name,
      loading: false,
      sort: 'orderAmount',
      sortType: 'DESC',
      tableList: [],
      pageNum: 1,
      page_size: 15,
      total: 0,
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
    }
  },
  created () {
    this.searchList()
  },
  mounted () {

  },
  methods: {
    //查询数据列表
    searchList () {
      this.laoding = true
      channelUserList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        // userName: this.list.userName,
        // cellPhone: this.list.cellPhone,
        groupId: this.id,
        sort: this.sort,
        sortType: this.sortType,
        beginTime: this.list.time == null ? '' : this.list.time[0],
        endTime: this.list.time == null ? '' : this.list.time[1],
      }).then(res => {
        this.loading = false
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          })
        }

      })
    },


    //自定义table表头
    renderHeader (h, { column, $index }) {
      if ($index === 3) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计成交笔数是指该分销员转发成功下单和有绑定关系的客户成功下单；"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>累计成交笔数</el-button>
          </el-tooltip>
        )
      }
      if ($index === 4) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计分销商品交易额是指该分销员转发成功下单和邀请成功的客户关联的订单支付成功的分销商品交易额；"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>累计分销商品交易额</el-button>
          </el-tooltip>
        )
      }
      if ($index === 8) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="客户数是指成功邀请并下单的客户数量"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>交易客户数</el-button>
          </el-tooltip>
        )
      }
    },


    // 表格数据进行排序
    tableSortChange (column) {
      console.log(column.prop, column.order)
      this.sort = column.prop
      if (column.order == null) {
        this.sortType = ''
        this.searchList()
        return
      }
      if (column.order == "ascending") {
        this.sortType = 'ASC'
      }
      if (column.order == "descending") {
        this.sortType = 'DESC'
      }
      this.searchList()
    },

    //查看分销员单据
    showDetails (row) {
      this.$router.push({
        path: '/distributionManagement/performance/documents',
        query: {
          userId: row.userId,
          userName: row.userName,
          cellPhone: row.cellPhone,
          groupName: row.groupName
        }
      })
    },



    untying (row) {
      this.$confirm('是否解绑该用户？解绑后，该用户再购买将不会给该分销员以及上级分销员返佣。', '用户解绑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        setUserBelong({
          id: row.id,
          isDelete: '1'
        }).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '解绑成功',
              type: 'success'
            });
          } else {
            this.$message.error(res.message);
          }
        })
      }).catch(() => { });
    },



    //返回
    goBack () {
      this.$router.push({
        path: '/distributionManagement/performance',
      })
    },

    //修改每页页数
    handleSizeChange (page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange (page) {
      this.pageNum = page
      this.searchList()
    },
    reset () {
      this.list.time = ''
      this.pageNum = 1
      this.searchList()
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 20px 20px;
  .main-title {
    display: flex;
    // justify-content: space-around;
    color: #f59a23;
    margin: 10px;
    // span{
    //   flex:1
    // }
    .span-yellow {
      flex: 1;
      color: #f59a23;
      font-weight: 500;
      font-size: 18px;
    }
  }
}
.header-con {
  box-sizing: border-box;
  padding-left: 20px;
  border-left: 6px solid #409EFF;
  font-size: 18px;
  font-weight:600;
  // line-height: 40px;
}

.fixedB {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
</style>